.bolg-single-inner {
    padding: 0 20px;
    margin-bottom: 50px;
}

.bolg-single-inner img {
    width: 100%;
    transition: all ease 0.5s;
}

.single-blog-footer {
    padding: 15px;
}

h5.blogs-title {
    padding-bottom: 12px;
}

a.blogs-read-more {
    margin-top: 10px;
    display: inline-block;
    font-size: 22px;
    color: #4e4e4e;
    font-weight: bold;
    transition: all ease 0.5s;
}

a.blogs-read-more:hover {
    letter-spacing: 1px;
    text-decoration: underline;
}


.bolg-single-inner a {
    display: block;
    overflow: hidden;
}

.bolg-single-inner a:hover img {
    transform: scale(1.2);
}



/* single blog posts */


.single-post-banner-image {
    padding-top: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}


h2.blogs-title {
    padding: 30px 0;
}

.post-contents p, .post-contents h3 {
    padding-bottom: 20px;
}