.slider_single img {
    max-width: 200px;
    margin: 0 auto;
    filter: grayscale(100);
    transition: all ease 0.5s;
    opacity: 0.5;
}


.slider_single img:hover{
    filter: grayscale(0);
    opacity: 1;
}


.slick-dots{
    bottom: -50px;
}

.slick-dots li button:before{
    font-size: 16px;
}