.form-field input, .form-field textarea {
    width: 100%;
    border: solid 1px #000;
    padding: 15px;
}

.form-container {
    max-width: 800px;
    margin: 0 auto;
}

.form-fields {
    width: 100%;
}

.form-field {
    width: 50%;
    padding: 15px;
}



.form-field.width-100 {
    width: 100%;
}

.form-field input[type="Submit"] {
    max-width: 200px;
    margin: 0 auto;
    display: block;
    cursor: pointer;
    background-color: #F9A828;
    color: #ffff;
    font-weight: bold;
    border: none;
    font-size: 16px;
    transition: all ease 0.5s;
    border: solid 2px #F9A828;
}

.form-field input[type="Submit"]:hover {
    background-color: #ffff;
    color: #f9a826;
}