.single_service {
    padding: 20px;
    transition: all ease 0.5s;
    height: 100%;
    cursor: pointer;
    box-shadow: 0px 0 60px 0 rgb(0 0 0 / 9%);
    margin: 16px;
}

.service-image {
    padding: 120px 0;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 10px;
}


.services_container .col-25{
    height: 100%;
}


.section_heading {
    padding-bottom: 40px;
}


.single_service img {
    max-width: 165px;
    margin: 0 auto 20px auto;
    min-height: 145px;
    border: solid 1px #f9a826;
    padding: 15px;
    
}

.services_container {
    margin: 0 -20px;
}


.single_service:hover {
    box-shadow: 0px 0 60px 0 rgb(0 0 0 / 25%);
}