.video_blk {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    margin-right: 40px;
    padding: 160px 0;
}


.ReactModal__Content {
    opacity: 0;
    transform: translateY(100px);
    transition: all 500ms ease-in-out;
  }
  
  .ReactModal__Content--after-open {
    opacity: 1;
    transform: translateY(0px);
  }
  
  .ReactModal__Content--before-close {
    opacity: 0;
    transform: translateY(100px);
  }


button.videobutton {
    width: 50px;
    height: 50px;
    color: transparent;
    border-radius: 50%;
    position: relative;
    background-color: #0ea2bd;
    border: none;
    cursor: pointer;
}

button.videobutton:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    height: 0;
    width: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-left: 15px solid #ffff;
    transform: translateX(18px) translateY(-8px);
    transition: all ease 0.5s;
}

.video_content_blk p {
    padding: 20px 0 15px 0;
}

ul.styled_lists li {
    padding-bottom: 10px;
    position: relative;
    font-size: 16px;
}

ul.styled_lists {
    padding: 0 0 0 20px;
}

ul.styled_lists li:after {
    content: '';
    position: absolute;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-left: 8px solid #030303;
    left: -16px;
    top: 5px;
}


button.videobutton_close {
    position: absolute;
    right: 0px;
    top: -40px;
    z-index: 999999999999;
    color: #fff;
    border: none;
    background-color: transparent;
    font-size: 30px;
    cursor: pointer;
}



button.videobutton:hover:after {
    border-left: 5px solid #ffff;
    border-right: 5px solid #ffff;
    border-top: none;
    border-bottom: none;
    width: 6px;
    height: 16px;
    left: -2px;
}